import React, { Component } from 'react';
import { Row, Col, Form, Select } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import { withRouter } from 'react-router';
// import { getQueryParams } from 'utils/urlUtil';
import { requiredRule } from 'utils/validateRules';

const { Option } = Select;

@Form.create({ name: 'FormSearchDormitoryRoom' })
class FormSearchBill extends Component {
  state = {};

  // componentDidMount = () => {
  //   const { form } = this.props;
  //   form.setFieldsValue(getQueryParams());
  // };

  formItemLayout = 6;

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      console.log(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <Col span={this.formItemLayout}>
            <Form.Item label="Năm tài chính">
              {getFieldDecorator('financialYear', {
                rules: [requiredRule],
              })(
                <Select placeholder="Chọn năm">
                  <Option value="2018">2018</Option>
                  <Option value="2019">2019</Option>
                </Select>
              )}
            </Form.Item>
          </Col>

          <Col span={this.formItemLayout}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default withRouter(FormSearchBill);



