import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getBills, deleteBill } from 'services/bill';
import FormSearchBill from './FormSearchBill';
import './style.scss';

class Bill extends Component {
  state = {
    reload: 0,
  };

  handleDelete = async id => {
    await deleteBill(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'STT',
        dataIndex: 'index',
        key: 'index',
        align: 'center'
      },
      {
        title: 'Tên loại hóa đơn',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Ký hiệu mẫu hóa đơn',
        dataIndex: 'templateCode',
        key: 'templateCode',
        align: 'center'
      },
      {
        title: 'Ký hiệu hóa đơn',
        dataIndex: 'code',
        key: 'code',
        align: 'center'
      },
      {
        title: 'Số tồn đầu kỳ từ',
        dataIndex: 'startRemainAmountFrom',
        key: 'startRemainAmountFrom',
        align: 'center'
      },
      {
        title: 'Số tồn đầu ký đến',
        dataIndex: 'startRemainAmountTo',
        key: 'startRemainAmountTo',
        align: 'center'
      },
      {
        title: 'Số phát hành trong kỳ từ',
        dataIndex: 'startProduceAmountFrom',
        key: 'startProduceAmountFrom',
        align: 'center'
      },
      {
        title: 'Số phát hành trong kỳ đến',
        dataIndex: 'startProduceAmountTo',
        key: 'startProduceAmountTo',
        align: 'center'
      },
      {
        title: 'Số hóa đơn xóa',
        dataIndex: 'deleteAmount',
        key: 'deleteAmount',
        align: 'center'
      },
      {
        title: 'Danh sách các số hóa đơn xóa',
        dataIndex: 'deletedItems',
        key: 'deletedItems',
      },
      {
        title: 'Số hóa đơn mất',
        dataIndex: 'lostAmount',
        key: 'lostAmount',
        align: 'center'
      },
      {
        title: 'Danh sách số hóa đơn mất',
        dataIndex: 'lostItems',
        key: 'lostItems',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        className: 'action-column',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#bill-management/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Hóa đơn">
        <CartHeadAction>
          <BtnAddNew href="#bill-management/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormSearchBill />
          </Col>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getBills}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <Bill />
  </Page>
);



