export const getSemesterById = id =>
  request({
    url: `/Semester/${id}`,
    method: 'GET',
  });

export const deleteSemester = id =>
  request({
    url: `/Semester/${id}`,
    method: 'DELETE',
  });

export const updateSemester = ({ id, label, isMain }) =>
  request({
    url: `/Semester/${id}`,
    data: { id, label, isMain },
    method: 'PUT',
  });

export const createSemester = ({ label, isMain = false }) =>
  request({
    url: `/Semester`,
    data: { label, isMain },
    method: 'POST',
  });



