import { request } from 'utils';

request.prefix = '/api/categories';

export const getYearEducations = ({ page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    url: '/YearEducation',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const getYearEducationById = id =>
  request({
    url: `/YearEducation/${id}`,
    method: 'GET',
  });

export const deleteYearEducation = id =>
  request({
    url: `/YearEducation/${id}`,
    method: 'DELETE',
  });

export const updateYearEducation = ({ id, fromYear, toYear }) =>
  request({
    url: `/YearEducation/${id}`,
    data: { id, fromYear, toYear },
    method: 'PUT',
  });

export const createYearEducation = ({ fromYear, toYear }) =>
  request({
    url: `/YearEducation`,
    data: { fromYear, toYear },
    method: 'POST',
  });



